  margin-bottom: 0.9375em;
}
.o-email-signup .a-text-input {
  max-width: 23.125rem;
}
.o-email-signup__buttons {
  display: flex;
  margin-top: 0.9375em;
  align-items: center;
  flex-wrap: wrap-reverse;
  gap: 0.9375em;
}
.o-email-signup .a-btn {
  text-align: inherit;
}
