/* ==========================================================================
   Design System
   Base styles
   ========================================================================== */
.m-list--unstyled,
.m-list--horizontal,
.m-list--links {
  padding-left: 0;
  list-style-type: none;
}
.m-list--unstyled .m-list__item,
.m-list--horizontal .m-list__item,
.m-list--links .m-list__item {
  margin-left: 0;
}

.m-list--horizontal .m-list__item {
  display: inline-block;
  margin-right: 0.25em;
  margin-bottom: 0;
}

@media only screen and (max-width: 37.5em) {
  .m-list--links .m-list__item {
    margin-bottom: 0;
  }
}

html[lang=ar] .m-list {
  padding-right: 0;
}