  }
}

@media print {
  .u-screen-only {
    display: none;
  }
}

.u-clearfix::after {
  content: "";
  display: table;
  clear: both;
}

.u-visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

@media only screen and (max-width: 37.5em) {
  .u-hide-on-mobile {
    display: none;
  }
}

@media only screen and (min-width: 37.5625em) {
  .u-show-on-mobile {
    display: none;
  }
}

.u-hidden {
  display: none !important;
}

.u-invisible {
